
// ** React Imports
import { Fragment, useState, useEffect } from 'react'
import UiLoader from '@components/ui-loader'
import { useForm } from 'react-hook-form'
import classnames from 'classnames'

// ** Store & Actions
import { getcricketTotal, clearData, getCricketContestadd, searchcricketcontest } from '../../store/actions'
import { useSelector, useDispatch } from 'react-redux'

// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Filter } from 'react-feather'
import DataTable from 'react-data-table-component'
import { Card, Row, Col, Label, Button, Spinner } from 'reactstrap'
import { getAllSeries, getAllFixture, collectseriesid, collectfixtureid, getval } from "../../../Report/CricketReport/store/actions"

// ** Styles
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import AppCollapse from '@components/app-collapse'
import BreadCrumbs from '@components/breadcrumbs'
import { getCricketFixture } from '../../../fixture/store/actions'

// ** Import Scrollbar Paginate Component
import { AsyncPaginate } from "react-select-async-paginate"

// ** Table Header
const CustomHeader = () => {
  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">
      <Card>
      </Card>
    </div>
  )
}

const UsersList = () => {

  const typeOptions = [
    { value: '1', label: 'public' },
    { value: '2', label: 'private' },
    { value: '4', label: '' }
  ]

  const prizeOptions = [
    { value: '1', label: 'coins' },
    { value: '2', label: 'coupons' },
    { value: '3', label: 'coupons,coins' },
    { value: '4', label: '' }
  ]

  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector((state) => state.contest)
  const store1 = useSelector((state) => state.contest)
  const sto = useSelector((state) => state.rep)

  // ** States

  const [searchTerm, setSearchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [paginate, setPaginate] = useState(0)
  const [sid, setSid] = useState(0)
  const [fid, setFid] = useState(0)
  const [val, setVal] = useState(false)
  const [empty, setEmpty] = useState(false)
  const [serilist, setSerilist] = useState("")
  const [fixlist, setFixlist] = useState("")
  const [refresh, setRefresh] = useState(false)
  const [click, setClick] = useState(false)
  const [error, setError] = useState(false)
  const [searchType, setSearchType] = useState('')
  const [searchPrize, setSearchPrize] = useState('')
  const [page, setPage] = useState(1)
  const [value] = useState(null)

  // ** Get data on mount

  useEffect(() => {
    dispatch(getcricketTotal(page))
  }, [page])


  useEffect(() => {
    const idea = `{"series_id":${sto.seriesid}, "fixture_id":"${sto.getval ? sto.fixtureid : ""}", "page":${currentPage}, "contest_type":"${searchType ? searchType : ""}" }`
    if (store.cricket_contest_add.length !== 0) {
      dispatch(getCricketContestadd(idea))
    } else {
      dispatch(searchcricketcontest(currentPage, searchType))
      dispatch(getCricketFixture(currentPage))
    }
    dispatch(getAllSeries())

  }, [paginate])


  useEffect(() => {
    if (store.isloading) {
      <Spinner />
    } else {
      setListData(store.cricket_contest)
    }
  }, [store.isloading])

  // ** Function in get data on page change
  const handlePagination = (page) => {
    setPaginate(paginate + 1)
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value)
    setRowsPerPage(value)
    setCurrentPage(1)
  }

  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  useEffect(() => {
    if (searchTerm) {
      const filterList = store.cricket_contest.filter((userItem) => {
        if (userItem.player_type) {
          return userItem.player_type.toLowerCase().includes(searchTerm.toLowerCase())
        }
      })

      setListData(filterList)
      setCurrentPage(1)
    } else {
      setListData(store.cricket_contest)
    }
  }, [searchTerm])

  if (store.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }
  const columns = [
    {
      name: '#',
      minWidth: '10px',
      selector: 's_no',
      sortable: true,
      cell: (row, pos) => `#${store.cricket_contest_add?.data?.length ? store.cricket_contest_add?.from + pos : store.searchCricketContest?.from + pos}`
    },
    {
      name: 'challenges name',
      minWidth: '160px',
      selector: 'contest_name',
      sortable: true,
      cell: (row) => (row.contest_name)
    },
    {
      name: 'Team A vs Team B',
      minWidth: '350px',
      selector: 'fixture_name',
      sortable: true,
      cell: (row) => (
        <Row>
          <Col>
            <img src={row.team_a_logo} style={{ height: '25px', width: '25px' }} />
          </Col>
          <div style={{width:'160px', overflow:'hidden', textOverflow:'ellipsis'}}>{row.fixture_name}</div>
          <Col>
            <img src={row.team_b_logo} style={{ height: '25px', width: '25px' }} />
          </Col>
        </Row>)
    },
    {
      name: 'challenges type',
      minWidth: '160px',
      selector: 'contest_type',
      sortable: true,
      cell: (row) => (row.contest_type)
    },
    {
      name: 'challenges caption',
      minWidth: '160px',
      selector: 'caption',
      sortable: true,
      cell: (row) => row.caption
    },
    {
      name: 'Max Entries',
      minWidth: '160px',
      selector: 'size',
      sortable: true,
      cell: (row) => row.size
    },
    {
      name: 'Prize type',
      minWidth: '160px',
      selector: 'prize_types',
      sortable: true,
      cell: (row) => row.prize_types
    },
    {
      name: 'Total prize',
      minWidth: '160px',
      selector: 'total_prize',
      sortable: true,
      cell: (row) => row.total_prize
    }
  ]

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = store.cricket_contest_add?.data?.length > 0 ? store.cricket_contest_add?.last_page : store.searchCricketContest?.data?.length > 0 ? store.searchCricketContest?.last_page : store.cricket_page.last_page

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }

  console.log(store)

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    if (store.cricket_contest_add?.data?.length >= 0) {
      return store.cricket_contest_add?.data
    } else if (store.searchCricketContest?.data?.length) {
      return store.searchCricketContest?.data
    } else if (listData && isFiltered) {
      return []
    }
  }

  const defaultValues = {
    reactSelect: null
  }
  const { handleSubmit } = useForm({ defaultValues })


  const fixture = sto?.fixture_name?.map(function (fix) {
    return { value: fix.id, label: fix.fixture_name }
  })

  const filterid = sto.series_name?.filter(s => s.series_name === serilist)
  const filterid2 = sto.fixture_name?.filter(f => f.fixture_name === fixlist)
  const filterall = sto.fixture_name?.map(f => f.fixture_name)

  useEffect(() => {
    console.log('Component Mounted')
    return () => {
      console.log('component Unmounted')
      dispatch(clearData())
    }
  }, [])

  useEffect(() => {
    dispatch(getAllFixture(serilist))
    console.log(sid)
  }, [serilist])

  const idea = `{"series_id":${serilist}, "fixture_id":"${click ? fixlist : ""}", "page":${1}, "contest_type":"${searchType ? searchType : ""}"}`
  const submit = (e) => {
    e.preventDefault()
    console.log(error)
    setVal(true)
    setEmpty(true)
    console.log(filterall.map(f => f === fixlist))
    setCurrentPage(1)
    dispatch(collectseriesid(serilist))
    dispatch(collectfixtureid(fixlist))
    if (serilist === "") {
      setError(false)
    } else {
      dispatch(getCricketContestadd(idea))
    }
  }

  useEffect(() => {
    if (store?.cricket_contest_add?.length !== 0) {
      setListData(store.cricket_contest_add)
    } else {
      if (store?.cricket_contest_add?.length === 0 && empty) {
        setListData(store?.cricket_contest_add)
      } else {
        setListData(store.cricket_contest)
      }
    }
  }, [submit])

  const serieslist = (e) => {
    setVal(false)
    setSerilist(e?.value)
    setError(false)
    setRefresh(true)
    dispatch(getval(false))
    setSid(filterid[0]?.id)
    setClick(false)
  }

  const fixturelist = (e) => {
    const datas = e.map(e => e.value)
    setFixlist(datas)
    setRefresh(false)
    dispatch(getval(true))
    setClick(true)
    setFid(filterid2[0]?.id)
  }

  const handleKeyPress = (e) => {
    if (e.keyCode === 13) {
      setCurrentPage(1)
      dispatch(getCricketContestadd(idea))
      dispatch(collectseriesid(serilist))
      dispatch(collectfixtureid(fixlist))
    }
  }

  const typeHandler = (value) => {
    setSearchType(value)
    setCurrentPage(1)
  }
  console.log(store1.cricket_total?.data?.total)
  // ************************************************************************************* //

  const options = []
  for (let i = 0; i < store1.cricket_total?.data?.total; i++) {
    if (page === store1.cricket_total?.data?.current_page) {
      options.push({
        value: `${store1.cricket_total?.data?.data[i]?.id}`,
        label: `${store1.cricket_total?.data?.data[i]?.series_name}`
      })
    }

  }
  const sleep = ms => new Promise(resolve => { setTimeout(() => { resolve() }, ms) })
  const optionsPerPage = 10

  const loadOptions = async (search, prevOptions, page) => {
    await sleep(1000)
    if (page === store1.cricket_total?.data?.current_page) {
      const nextData = store1.cricket_total?.data?.data?.map((nextData) => { return { value: nextData.id, label: nextData.series_name } })
      const scrollLength = [...prevOptions, ...nextData]
      let filteredOptions
      if (!search) {
        filteredOptions = scrollLength.length > 10 ? scrollLength : options
      } else {
        const searchLower = search.toLowerCase()
        filteredOptions = [...scrollLength.length > 10 ? scrollLength : options].filter(({ label }) => label.toLowerCase().includes(searchLower)
        )
      }

      const hasMore = Math.ceil(store1.cricket_total?.data?.total / optionsPerPage) > page
      const slicedOptions = filteredOptions.slice(
        (page - 1) * optionsPerPage,
        page * optionsPerPage
      )

      return {
        options: slicedOptions,
        hasMore
      }
    }
  }
  const defaultAdditional = {
    page: 1
  }

  const loadPageOptions = async (q, prevOptions, { page }) => {
    setPage(page)
    const { options, hasMore } = await loadOptions(q, prevOptions, page)
    return {
      options: [...options],
      hasMore,

      additional: {
        page: page + 1
      }
    }
  }

  // ************************************************************************************* //

  const data = [
    {
      title: <div><Filter size={18} /> Filter...</div>,
      content: (
        <>
          <Row>
            <Col className='mb-1' md='6' sm='12'>
              <Label>series</Label>
              <AsyncPaginate
                menuPortalTarget={document.querySelector('body')}
                theme={selectThemeColors}
                className={classnames('react-select', { 'is-invalid': serilist === "" && val })}
                classNamePrefix='select'
                additional={defaultAdditional}
                value={value?.label}
                loadOptions={loadPageOptions}
                onChange={(e) => serieslist(e)}
                onKeyDown={(e) => handleKeyPress(e)}
              />
              {serilist === "" && val ? <h5 className='text-danger'>Please enter a value</h5> : ""}
            </Col>
            <Col className='mb-1' md='6' sm='12'>
              <Label>Fixture</Label>
              <Select
                isDisabled={store.fixloader}
                // isLoading={store.fixloader}
                onChange={(e) => fixturelist(e)}
                theme={selectThemeColors}
                value={refresh ? "" : fid}
                className='react-select'
                classNamePrefix='select'
                isMulti
                // defaultValue={fixture[0]}
                options={fixture ? fixture : ""}
                placeholder='Select cricket fixture'
                isClearable={true}
              />
            </Col>
          </Row>
          <Row>
            <Col className='mb-1 mt-1' md='6' sm='12'>
              <Label>Challenge Type:</Label>
              <Select
                menuPortalTarget={document.querySelector('body')}
                onChange={(e) => typeHandler(e?.label)}
                theme={selectThemeColors}
                className='react-select'
                classNamePrefix='select'
                options={typeOptions}
                placeholder='search challenge type...'
                isClearable={true}
                onKeyDown={(e) => handleKeyPress(e)}
              />
            </Col>
            <Col className='mb-1 mt-1' md='6' sm='12'>
              <Label>Prize Type:</Label>
              <Select
                menuPortalTarget={document.querySelector('body')}
                onChange={(e) => setSearchPrize(e?.label)}
                theme={selectThemeColors}
                className='react-select'
                classNamePrefix='select'
                options={prizeOptions}
                placeholder='search prize type...'
                isClearable={true}
                onKeyDown={(e) => handleKeyPress(e)}
              />
            </Col>
          </Row>
          <Button.Ripple
            color='primary'
            onClick={(e) => handleSubmit(submit(e))}
          >submit</Button.Ripple>
        </>
      )
    }
  ]


  console.log(currentPage, searchType, searchPrize)

  return (
    <Fragment>
      <BreadCrumbs breadCrumbTitle='Cricket Challenges' breadCrumbActive='List' />
      <AppCollapse data={data} type='border' accordion className='mb-1' />
      <Card>
        <DataTable
          noHeader
          pagination
          subHeader
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              handlePerPage={handlePerPage}
              rowsPerPage={rowsPerPage}
              searchTerm={searchTerm}
              currentPage={currentPage}
              handleFilter={handleFilter}
              setListData={setListData}
              cricket_contest={store.cricket_contest}
              cricket_contest_add={store.cricket_contest_add}
              setCurrentPage={setCurrentPage}
            />
          }
        />
      </Card>
    </Fragment>
  )
}

export default UsersList