import http from '../../../../configs/httpConfig'

export const clearData = () => {
  return {type:'CLEAR_DATA'}
}

export const getfootballcontestdownload = () => {
    return async () => {
      await http
        .get('/football/reports/contestReport?download=true', { responseType: 'blob' })
        .then((response) => {
          const url = window.URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', 'football_contest.csv') //or any other extension
          document.body.appendChild(link)
          link.click()
        })
    }
  }

