export const getfootballcontest = (page) => {
    return async (dispatch) => {
        // dispatch(getcontestdatainit())
      await http.get(`/football/contests/list?page=${page}`).then((response) => {
        dispatch({
          type: 'GET_FOOTBALL_CONTEST',
          data: response.data.data
        })
        console.log(response)
      })
    }
  }

